* {
    box-sizing: border-box;
    list-style: none;
}

.title-APImeli {
    /* width: 14.5rem; */
    font-family: 'Anton', sans-serif;
    font-size: 30px;
    color: #fff;
    letter-spacing: 0.15vw;
    text-shadow: 0.1vw 0.15vw 0.2vw #000;
    padding: 1vw;
    margin-bottom: 0 !important;
    /* background-color: blue; */
}

.section-formulario {
    width: 100%;
    background-color: #FFF159;
}

.section-formulario form {
    width: 70%;
    /* background-color: red; */
}

.section-formulario form input {
    width: 55%;
    height: 39px;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.2);
    border: none !important;
}

.section-formulario form input {
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.2) !important;
    border: none !important;
}

.section-formulario form button,
.section-formulario form:focus,
 select:focus, select, 
 .section-formulario form input.form-control:focus {
    outline:none !important;

    outline-width: 0 !important;

    box-shadow: none !important;

    -moz-box-shadow: none !important;

    -webkit-box-shadow: none !important;
}


::placeholder {
    color: rgb(184, 184, 184) !important;
    opacity: .45 !important;
}

.fa-magnifying-glass {
    opacity: .55;
}

.container__condition-section-producto {
    width: auto;
    display: flex !important;
    justify-content: center;
    margin: auto;
    /* background-color: green; */
}

.section-producto {
    width: 53% !important;
    height: auto;
    /* background-color: blue; */
    /* margin: auto; */
    display: flex;
    flex-direction: column;
}

.section-producto ul {
    width: auto;
    height: auto;
    background-color: #fff;
    padding-left: 0;
}

.container-product {
    display: flex;
    align-items: center;
}

.container-imgProducto {
    width: 160px;
    height: 160px;
    /* background-color: red ; */
    
}

.container-imgProducto img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.container_info-product {
    width: 40vw;
    height: auto;
    margin-left: 1.5vw;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.title-product h2 {
    -webkit-box-orient: vertical;
    display: -webkit-box; 
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-word;
    -webkit-line-clamp: 2;
    
    font-size: 20px;
    color: #333;
    line-height: 1.4;
    text-align: left;
}

.container_info-product a {
    color: #3483fa;
    text-decoration: none;
}

.container_info-product a:hover {
    color: blue;
}

.price-product {
    font-size: 24px;
    line-height: 1.25;
    position: relative;
    top: 0.4vw;
    margin-top: 0.5vw;
}

.cuotasProducto, .envio-producto {
    color: #00a650;
}

.cuotasProducto, .envio-producto {
    font-size: 14px;
}

.cuotasProducto {
    font-weight: 400;
}

.container-envio-condicion {
    width: auto;
    height: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* background-color: orange; */
}

.envio-producto {
    font-weight: 600;
    /* position: relative;
    bottom: 0.55vw; */
}

.product-condition {
    color: #666;
    font-size: 14px;
    /* background-color: yellow; */
    margin-top: -0.8vw !important;
}